___CSS_LOADER_EXPORT___.push([module.id, "\n\n/* .test1{\n     border-left: 6px solid red !important;\n} */\n.VueTables__sort-icon{\n    float: right;\n    font-size: 15px;\n    font-weight: 600;\n}\n.nonLogo{\n    font-size: 14pt;\n}\n\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/application/appDataTable.vue"],"names":[],"mappings":";;AAuIA;;GAEG;AAEH;IACI,YAAY;IACZ,eAAe;IACf,gBAAgB;AACpB;AACA;IACI,eAAe;AACnB","sourcesContent":["\n<template>\n    <div>\n\n        <v-client-table ref='table' :data=\"appValues\" :columns=\"appDatatableColumns\" :options=\"optionsDataTable\">      \n                <template #createdDate=\"props\">\n                <span>{{ new Intl.DateTimeFormat(\"tr-TR\",options).format(new Date(props.row.createdDate))}}</span>\n            </template>\n            <template #name=\"props\">             \n                <div  :style=\"{ display: 'flex', 'border-left': `6px solid ${props.row.color} `}\">\n                    <div class=\"col-4\" style='text-align:center'>\n                        <!-- <img v-if=\"!props.row.logo\" src='../../../../images/is-bankasi-logo.jpg'\n                            class=\"card-logo avatarLogo\"> -->\n                            <i v-if=\"!props.row.logo\" class='mdi mdi-image-outline  nonLogo'></i>\n                        <img v-if=\"props.row.logo\" :src='props.row.logo' class=\"card-logo avatarLogo\">\n                    </div>\n                    <div>{{ props.row.name }}</div>\n                </div>\n            </template>\n            <template #actions=\"props\">\n                <div v-if=\"props.row.synced\" class=\"col-lg-1 p-0 dot-icon\">\n                    <i class=\"mdi mdi-dots-vertical vertical-dot-icon\" id=\"dropdownMenuButton1\"\n                        data-bs-toggle=\"dropdown\" aria-expanded=\"false\" data-bs-target=\"#dropdown-menu\"></i>\n                    <ul class=\"dropdown-menu\" aria-labelledby=\"dropdownMenuButton1\">\n                                    <li><a class=\"dropdown-item\" href=\"#\" @click=\"openApp(props.row.ID)\"><i\n                                    class=\"mdi mdi-open-in-app\"></i>Open</a></li>\n                        <li><a class=\"dropdown-item\" href=\"#\" @click='updateApp(props.row)'><i\n                                    class=\"mdi mdi-pencil-outline\"></i>Edit</a></li>\n                        <li><a class=\"dropdown-item list-group-item-danger\" @click=\"deleteApp(props.row)\"><i\n                                    class=\"mdi mdi-delete-outline\"></i>Delete</a></li>\n                    </ul>\n                </div>\n            </template>\n        </v-client-table>\n    </div>\n</template>\n\n<script setup lang=\"ts\">import { container } from 'tsyringe';\nimport { defineAsyncComponent, ref } from 'vue';\nimport { IApplication, IApplicationDetails } from '../../../../domain/model/models';\nimport { IDialog } from '../../../../domain/presentation/IDialog';\nimport { IRouter } from '../../../../domain/presentation/IRouter';\nimport { IObjectUseCase } from '../../../../domain/useCase/IObjectUseCase';\nimport { IUseCaseExecutor } from '../../../../domain/useCase/IUseCaseExecutor';\nimport { ILocalization } from \"../../../../domain/infrastructure/ILocalization\";\nimport { IViewModel } from '../../../../domain/viewModel/IViewModel';\nimport { computed } from '@vue/reactivity';\n\nconst appDatatableColumns = ref(['name', 'description', 'createdBy', 'createdDate', 'modules', 'pages', 'templates', 'actions']);\nconst dialog = container.resolve<IDialog>(IDialog);\nconst localization = container.resolve<ILocalization>(ILocalization);\nconst objectUseCase = container.resolve<IObjectUseCase>(IObjectUseCase);\nconst router = container.resolve<IRouter>(IRouter);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nlet options :any = { month:'2-digit',day:'2-digit', year:'numeric', hour: 'numeric', minute: 'numeric',second: 'numeric'};\ninterface IAppValues {\n    ID: IApplication[\"ID\"];\n    name: IApplication[\"name\"];\n    description: IApplication[\"description\"];\n    logo: IApplication[\"logo\"];\n    color: IApplication[\"color\"];\n    createdBy: IApplication[\"createdBy\"],\n    createdDate: IApplication[\"createDate\"],\n    modules: IApplicationDetails[\"moduleCount\"],\n    pages: IApplicationDetails[\"pageCount\"],\n    templates: IApplicationDetails[\"templateCount\"],\n    synced: IApplication[\"synced\"],\n};\n\nconst appValues = computed(() => {\n    return viewModel.apps.map<IAppValues>(app => {\n        const retVal:IAppValues = {\n            ID: app.ID,\n            name: app.name,\n            description: app.description,\n            logo: app.logo,\n            color: app.color,\n            createdBy: app.createdBy,\n            createdDate: app.createDate,\n            synced: app.synced,\n            modules: app.details?.moduleCount || 0,\n            pages: app.details?.pageCount || 0,\n            templates: app.details?.templateCount || 0,\n        };\n        return retVal;\n    });\n});\n\nconst optionsDataTable = ref({\n    filterable: false, // omit the `id` column,\n    perPage: 5,\n    perPageValues: [],\n    sortIcon:{\n            base:'mdi',\n            is:'mdi mdi-unfold-less-horizontal',\n            up: 'mdi mdi-arrow-up',\n            down: 'mdi mdi-arrow-down'\n          },\n\n    // cellClasses:{ name: [ { class:'test1', condition: (row: { color: string;  }) => row.color } ] \n    // },\n\n    headings: {\n        name: localization.get('applicationListNameHeader'),\n        description: localization.get('applicationListDescriptionHeader'),\n        createdBy: localization.get('applicationListcreatedByHeader'),\n        createdDate: localization.get('applicationListcreatedDateHeader'),\n        modules: localization.get('applicationListmodulesHeader'),\n        pages: localization.get('applicationListpagesHeader'),\n        templates: localization.get('applicationListtemplatesHeader'),\n        actions: localization.get('applicationListactionsHeader')\n    },\n    texts: {\n        count: '',\n    },\n\n});\n\n\nfunction openApp(id:any){\n     router.push(`/studio/${viewModel.user!.selectedOrganization!.id}/${id}`);\n}\nfunction updateApp(app: IApplication) {\n\n    dialog.showDialog(defineAsyncComponent(() => import(`../../components/application/addOrUpdateApp.vue`)), { closable: true, title: \"Update Application\" }, { app });\n}\nfunction deleteApp(app: IApplication) {\n    executor.execute(async () => {\n        await objectUseCase.deleteObject(app.ID);\n    }, { loading: true });\n}\n</script>\n<style >\n\n/* .test1{\n     border-left: 6px solid red !important;\n} */\n\n.VueTables__sort-icon{\n    float: right;\n    font-size: 15px;\n    font-weight: 600;\n}\n.nonLogo{\n    font-size: 14pt;\n}\n\n</style>"],"sourceRoot":""}]);
